import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import '../../models/feed';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class FeedsService {
  constructor(private http: HttpClient) {}

  feeds = new BehaviorSubject<[Feed]>(null);

  getAll() {
    return this.http.get(baseUrl + '/feed').subscribe((res) => {
      if (res['data']) {
        this.feeds.next(res['data']);
      }
    });
  }

  getOne(itemId) {
    return this.http.get(baseUrl + `/feed/${itemId}`);
  }

  addOne(form) {
    return this.http.post(baseUrl + '/feed', form);
  }

  updateOne(form, itemId) {
    return this.http.put(baseUrl + `/feed/${itemId}`, form);
  }

  deleteOne(itemId) {
    return this.http.delete(baseUrl + `/feed/${itemId}`);
  }
}
