import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormArray } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

import { FeedsService } from '../feeds.service';

@Component({
  selector: 'app-add-feed',
  templateUrl: './add-feed.component.html',
  styleUrls: ['./add-feed.component.scss'],
})
export class AddFeedComponent implements OnInit {
  image;
  constructor(
    private fb: FormBuilder,
    private feedsService: FeedsService,
    private toastr: ToastrService,
    private router: Router
  ) {}

  form = this.fb.group({
    image: [],
    name: this.fb.group({
      en: ['', Validators.required],
      ar: ['', Validators.required],
    }),
    description: this.fb.group({
      en: ['', Validators.required],
      ar: ['', Validators.required],
    }),
  });

  ngOnInit(): void {
  }

  onSubmit() {
    console.log(this.form.value);
    const formData = new FormData();

    for (const key in this.form.value) {
      if (this.form.value[key] instanceof File) {
        formData.append(key, this.form.value[key]);
      } else {
        // contains object
        formData.append(key, JSON.stringify(this.form.value[key]));
      }
    }
    this.feedsService.addOne(formData).subscribe(
      (res) => {
        console.log(res);
        this.image = null;
        this.form.reset();
        this.toastr.info(res['message'], 'Info');
        this.router.navigate(['/feeds']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }
}
