import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FeedsComponent } from './feeds.component';
import { AddFeedComponent } from './add-feed/add-feed.component';
import { EditFeedComponent } from './edit-feed/edit-feed.component';

const routes: Routes = [
  { path: '', component: FeedsComponent },
  { path: 'add', component: AddFeedComponent },
  { path: 'edit/:id', component: EditFeedComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class FeedsRoutingModule {}
