import { Component, OnInit, AfterViewInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { FeedsService } from '../feeds.service';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-edit-feed',
  templateUrl: './edit-feed.component.html',
  styleUrls: ['./edit-feed.component.scss'],
})
export class EditFeedComponent implements OnInit, AfterViewInit {
  image;
  feedId;
  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private feedsService: FeedsService,
    private toastr: ToastrService,
    private router: Router
  ) {}

  form = this.fb.group({
    image: [],
    name: this.fb.group({
      en: ['', Validators.required],
      ar: ['', Validators.required],
    }),
    description: this.fb.group({
      en: ['', Validators.required],
      ar: ['', Validators.required],
    }),
  });

  ngOnInit(): void {
    this.feedId = this.route.snapshot.paramMap.get('id');
    this.getOne(this.feedId);
  }
  ngAfterViewInit() {}

  getOne(id) {
    this.feedsService.getOne(id).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.image = res['data']['image'];
        this.form.get('image').patchValue(res['data']['image']);
        this.form.get('name.en').patchValue(res['data']['name']['en']);
        this.form.get('name.ar').patchValue(res['data']['name']['ar']);
        this.form
          .get('description.en')
          .patchValue(res['data']['description']['en']);
        this.form
          .get('description.ar')
          .patchValue(res['data']['description']['ar']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  onSubmit(formValue) {
    const formData = new FormData();

    for (const key in formValue) {
      if (formValue[key] instanceof File) {
        formData.append(key, formValue[key]);
      } else {
        // contains object
        formData.append(key, JSON.stringify(formValue[key]));
      }
    }

    this.feedsService.updateOne(formData, this.feedId).subscribe(
      (res) => {
        console.log(res);
        this.image = null;
        this.form.reset();
        this.toastr.info(res['message'], 'Info');
        this.router.navigate(['../']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }
}
